.jsa-theme-minimal {
  background-color: var(--jsa-min-dl-bg, white);
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal {
    background-color: var(--jsa-min-dl-bg-darkmode, rgba(9, 39, 44, 0.87));
  }
}
.jsa-theme-minimal dt {
  display: flex;
  align-items: center;
  position: relative;
  font-family: var(--jsa-min-font-family, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif);
  border-top: 1px solid var(--jsa-min-dt-border-color, #e3e3e3);
}
@media (prefers-color-scheme: dark) {
