    border-top: 1px solid var(--jsa-min-dt-border-color-darkmode, rgba(74, 209, 205, 0.87));
  }
}
.jsa-theme-minimal dt.active {
  border-bottom: none;
  border-top: 1px solid var(--jsa-min-dt-border-color, #e3e3e3);
  background-color: var(--jsa-min-dt-bgcolor-active, white);
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal dt.active {
    border-top: 1px solid var(--jsa-min-dt-border-color-darkmode, rgba(74, 209, 205, 0.87));
    background-color: var(--jsa-min-dt-bgcolor-active-darkmode, black);
  }
}
.jsa-theme-minimal dt.active a {
  color: var(--jsa-min-dt-anchor-active-color, rgba(0, 150, 136, 0.87));
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal dt.active a {
    color: var(--jsa-min-dt-anchor-active-color-darkmode, rgba(74, 209, 205, 0.87));
  }
}
.jsa-theme-minimal dt:first-of-type {
  border-top: 1px solid var(--jsa-min-dt-border-color, #e3e3e3);
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal dt:first-of-type {
    border-top: 1px solid var(--jsa-min-dt-border-color-darkmode, rgba(74, 209, 205, 0.87));
  }
}
.jsa-theme-minimal dt:last-of-type.active {
  border-bottom: none;
}
.jsa-theme-minimal dt a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: var(--jsa-min-font-family, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif);
  font-weight: var(--jsa-min-dt-font-weight, 600);
  color: var(--jsa-min-dt-anchor-color, rgba(0, 0, 0, 0.87));
  text-decoration: var(--jsa-min-dt-anchor-decoration, none);
  width: 100%;
  padding: var(--jsa-min-dt-padding, 1em);
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal dt a {
    color: var(--jsa-min-dt-anchor-color-darkmode, rgba(186, 249, 247, 0.87));
  }
}
.jsa-theme-minimal dt a:hover {
  color: var(--jsa-min-dt-anchor-hover-color, rgba(0, 150, 136, 0.87));
  text-decoration: var(--jsa-min-dt-anchor-decoration-hover, none);
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal dt a:hover {
    color: var(--jsa-min-dt-anchor-hover-color-darkmode, rgba(74, 209, 205, 0.87));
  }
}
.jsa-theme-minimal dt a[aria-expanded=true]::after {
  content: "-";
}
.jsa-theme-minimal dt a::after {
  content: "+";
  display: inline-block;
  position: absolute;
  top: calc(50% * (var(--jsa-min-dt-expand-icon-size, 18px)));
  right: var(--jsa-min-dt-expand-icon-right, 0);
  font-size: var(--jsa-min-dt-expand-icon-size, 18px);
  color: currentColor;
}
.jsa-theme-minimal dd {
  max-height: 0;
  font-family: var(--jsa-min-font-family, system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif);
  overflow: hidden;
  padding: 0;
  margin: 0;
  transition: max-height 0.4s cubic-bezier(0.4, 0, 0.2, 1), padding 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
@media (prefers-color-scheme: dark) {
  .jsa-theme-minimal dd {
    background-color: var(--jsa-min-dd-bgcolor-darkmode, rgb(23, 23, 23));
  }
  .jsa-theme-minimal dd > * {
    color: var(--jsa-min-dd-text-color-darkmode, rgba(186, 249, 247, 0.87));
  }
}

